.header {
  background-image: $gradientOrangeToRed;
  padding: 20px 0px;
  &__wrap {
    @include flex(center, space-between); }
  &__logo {
    transition: .3s;
    &:hover {
      transform: scale(1.5); } }
  &__nav {
    @include flex(flex-start, flex-start);
    li {
      margin-right: 30px;
      &:last-child {
        margin-right: 0; } }
    &-link {
      color: $white;
      font-size: 18px;
      text-transform: uppercase;
      text-decoration: none;
      position: relative;
      transition: .2s ease-in-out;
      &:after, &:before {
        content: '';
        position: absolute;
        width: 0;
        height: 1px;
        background-color: $white;
        transition: .2s ease-in-out; }
      &:after {
        top: 0;
        left: 0; }
      &:before {
        right: 0;
        bottom: 0; }
      &:hover {
        color: $black;
        &:after, &:before {
          width: 100%; } } } } }
